import React from 'react';
import Layout from '../../components/Layout';
import { SectionTitle, Pill } from '../../styles';
import { ProjectItem, ProjectTitle, SkillContainer, ImgContainer } from './styles';
const Projects = ({ user }) => {
  return (
    <Layout user={user}>
      <div>
        <SectionTitle>Mina Projekt</SectionTitle>
        <ul>
          {user.projects.map((project, i) => (
            <ProjectItem key={i}>
              <ProjectTitle>{project.name}</ProjectTitle>
              <p>{project.summary}</p>
              <ImgContainer><img src={project.images[0].resolutions.desktop.url} width='100%' height='auto'></img>
              </ImgContainer>
              <SkillContainer>
                {[...project.languages, ...project.libraries].map((item, j) => (
                  <Pill key={j}>{item}</Pill>
                ))}
              </SkillContainer>
            </ProjectItem>
          ))}
        </ul>
      </div>
    </Layout>
  );
};


export default Projects;
